
import { mavonEditor } from "mavon-editor";
import "mavon-editor/dist/css/index.css";
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import axios from 'axios'
export default {
name: "edit",
props: ["id"],
components: {
Group,
XTextarea,
XButton,
mavonEditor
},
data() {
return {
topic: {
title: "",
content: ""
},
toolbars: {
bold: true,
italic: true,
header: true,
underline: true,
ol: true,
ul: true,
link: true,
imagelink: true,
code: true,
fullscreen: true,
htmlcode: true,
help: true,
navigation: true,
alignleft: true,
aligncenter: true,
alignright: true,
preview: true
}
};
},
methods: {

uploadImage:function(pos,file){
var app = this


console.log(pos);
console.log(file);

var params = new ForData();
params.addend('smfile',file)
axios.post('https://vip1.loli.net',params,{

headers: { 'Content-Type': 'multipart/form-data' },
}).then(function(res){


app.$refs.md.$img2Url(pos,res.data.data.url);
})
},
edittopic: function() {

var app = this;
this.$http
.post("topics/update", {
topic_id: this.topic.id,
title: this.topic.title,
content: this.topic.content,
accesstoken: this.$store.state.accesstoken,
tab: "dev"
})
.then(function(res) {


app.$router.back();
});
}
},
beforeRouteEnter: function(to, from, next) {
next(function(vm) {
vm.$http.get("/topic/" + to.params.id).then(function(res) {
vm.topic = res.data.data;
});
});
},
beforeRouteUpdate: function(to, from, next) {
var app = this;
app.$http.get("/topic/" + to.params.id).then(function(res) {
app.topic = res.data.data;
});
next();
}
};



