import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);
const store = new Vuex.Store({
    state:{
      isLogined:false,//默认是未登录
      isShowMore:false, //actionsheet默认不显示
      loginname:'',//登录名，默认为空
      accesstoken: '',//token
      author_id:'',
      unLoginedMoreMenus:{ //未登录的菜单
        login: '登录',
        // 用户id
        
      },
    LoginedMoreMenus: {  //登录状态的菜单
        create:'发表新话题',
        center:'个人中心',
        msg:'消息通知',
        logout:'登出'
      },
      topics:[
        {name:'all',desc:'全部'},
        {name:'good',desc:'精华'},
        {name:'share',desc:'分享'},
        {name:'ask',desc:'问答'},
        {name:'job',desc:'招聘'}
      ]
    },
    getters:{
        moreMenus:function(state){
            // 如果用户是登录状态，显示个人中心，如果是未登录状态，显示登录
            if(state.isLogined){
              return state.LoginedMoreMenus;
            }
            return state.unLoginedMoreMenus;
          }
    },
    mutations:{
        mutationIsShowMore:function(state,payload){
            // state表示当前store对象的state数据，payload表示函数调用时传入的值
            state.isShowMore=payload;
        },
        mutationIsLogin:function(state){
          state.isLogined=true
        },
        mutationLoginname:function(state,payload){
          state.loginname=payload;
        },
        mutationAccesstoken:function(state,payload){
          state.accesstoken=payload;
        },
        mutationLogout:function(state){
          state.isLogined=false;
          state.loginname='';
          state.accesstoken = '';
      },
        // 修改author_id 
      mutationAuthorId: function (state,payload) {
        state.author_id = payload;
        }
    },
    actions:{}
})
export default store;


